<template>
  <div id="Home">
    <Rain/>
    <div class="lightning"></div>
    <div class="central">
      <img class="title-logo" src="@/assets/images/logo.svg" alt="">
      <br>
      <br>
      <br>
      <br>
      <h1 class="h1">Dyuksha '20</h1>
      <br>
      <br>
      <br>
      <h3 class="caption">February</h3>
      <h3 class="caption"><small>14 | 15 | 16</small></h3>
      <br>
      <h4 class="countdown" v-html="countdown"><small>{{countdown}}</small></h4>
    </div>
  </div>
</template>

<script>
import Rain from '@/components/Rain'
export default {
  name: 'Home',
  components: {
    Rain
  },
  data () {
    return {
      countdown: '...'
    }
  },
  mounted () {
    var countDownDate = new Date('Feb 14, 2020 00:00:00').getTime()
    // Update the count down every 1 second
    var x = setInterval(() => {
      // Get today's date and time
      var now = new Date().getTime()

      // Find the distance between now and the count down date
      var distance = countDownDate - now

      // Time calculations for days, hours, minutes and seconds
      var days = Math.floor(distance / (1000 * 60 * 60 * 24))
      var hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60))
      var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60))
      var seconds = Math.floor((distance % (1000 * 60)) / 1000)

      // Display the result in the element with id="demo"
      this.countdown = days + 'd ' + hours + 'h ' + minutes + 'm ' + seconds + 's '

      // If the count down is finished, write some text
      if (distance < 0) {
        clearInterval(x)
        this.countdown = '<u>Great Things Are Here</u>'
      }
    }, 1000)
  }
}
</script>

<style>
@import url("https://fonts.googleapis.com/css?family=Press+Start+2P");
@import url("https://fonts.googleapis.com/css?family=Blinker&display=swap");

#app {
  position: relative;
  background: linear-gradient(to bottom, #202020, #111119);
  max-width: 100vw;
  height: 200vh;
  font-family: "Press Start 2P", sans-serif;
  overflow-x: hidden;
  overflow-y: auto;
  margin: 0;
}

.title-logo{
  width: 100px;
  -webkit-filter: drop-shadow(2px 4px 0 #272324) drop-shadow(2px 2px 0 #E86F68);
  filter: drop-shadow(2px 4px 0 #272324) drop-shadow(2px 2px 0 #E86F68);
}

#Home{
  background: linear-gradient(to bottom, #202020, #111119);
  position: fixed;
  height: 100vh;
  width: 100vw;
  top:0;
  text-align: center;
}

.palette div:nth-child(1) {
  background: #272324;
  border: 2px solid #E4D8B4;
}

.palette div:nth-child(2) {
  background: #E4D8B4;
}

.palette div:nth-child(3) {
  background: #E86F68;
}

.central {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 5;
}

.h1 {
  display: inline;
  color: #E4D8B4;
  line-height: 50px;
  font-size: 40px;
  position: relative;
  text-align: center;
  text-transform: uppercase;
  -webkit-filter: drop-shadow(2px 4px 0 #272324) drop-shadow(2px 2px 0 #E86F68);
  filter: drop-shadow(2px 4px 0 #272324) drop-shadow(2px 2px 0 #E86F68);
}

.caption,.countdown{
  color: #E4D8B4;
}
.countdown{
  font-size: 20px;
  line-height: 30px;
}
.lightning {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: absolute;
  animation: flash 5s linear infinite;
}

@keyframes flash {
  0%,
  93%,
  96% {
    background-color: transparent;
  }
  94%,
  97%,
  98% {
    background-color: #E4D8B4;
  }
  96%,
  99%,
  100% {
    background-color: #E86F68;
  }
}

@media (max-width: 600px) {
  .h1 {
    font-size: 40px;
    line-height: 50px;
  }
}
</style>



