<template>
  <div id="Navbar" :class="{active : isActive}">
    <button class="nav-btn" @click = "myFilter">
      -- MENU --
    </button>
    <div class='nav' :class="{active : isActive}">
      <ul class="nav-items">
        <li class="nav-link"><v-link href="/">Home</v-link></li>
        <li class="nav-link"><v-link href="/events">Events</v-link></li>
        <li class="nav-link"><v-link href="/generals">Generals</v-link></li>
        <li class="nav-link"><v-link href="/workshops">Workshops</v-link></li>
        <li class="nav-link"><v-link href="/highlights">Highlights</v-link></li>
        <li class="nav-link"><v-link href="/contact">Contact</v-link></li>
        <li class="nav-link"><v-link href="/team">Team</v-link></li>
      </ul>
    </div>

  </div>
</template>

<script>
/* eslint-disable no-useless-escape */
import VLink from '@/components/VLink'

export default {
  name: 'Navbar',
  data () {
    return {
      isActive: false
    }
  },
  components: {
    VLink
  },
  methods: {
    /* eslint-disable */
    myFilter: function () {
        this.isActive = !this.isActive
      // some code to filter users
    }
  }
}
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css?family=Press+Start+2P");
*{
  font-family: "Press Start 2P", sans-serif;
}
#Navbar{
  margin: 0;
  position: fixed;
  z-index: 20;
  top:0;
  right:0;
  padding: 0;
  height: auto;
  width: 100vw;
}
.nav{
  margin: 0;
  position: absolute;
  width: 0;
  height: 0;
  background: linear-gradient(to bottom, #202020, #333333, #111119);
  transition: all 0.2s linear 0s;
  border-bottom-left-radius: 50%;
  overflow: hidden;
  right:0;
}
.active{
  width: 100vw;
  height: 100vh;
  border-bottom-left-radius:0;

}
.nav-btn{
  padding: 20px;
  font-family: "Press Start 2P", sans-serif;
  font-size: 12px;
  color: #E4D8B4;
  background: rgb(0,0,0,0);
  border: none;
  cursor: pointer;
  outline: none;
  position: absolute;
  z-index: 21;
  right:0;
}
.nav-items{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-55%,-50%);
  list-style: none;
  text-align: center
}
.nav-link{
  font-size: 25px;
  line-height: 50px;
}

</style>



