import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Navbar from '@/components/Navbar'
import Highlights from '@/components/Highlights'
import Events from '@/components/Events'
import Team from '@/components/Team'
import Contact from '@/components/Contact'
import Departments from '@/components/Departments'
import DetailsEvents from '@/components/DetailsEvents'
import DetailsGenerals from '@/components/DetailsGenerals'
import DetailsWorkshops from '@/components/DetailsWorkshops'
import Generals from '@/components/Generals'
import Workshops from '@/components/Workshops'
import Axios from '@/components/Axios'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/nav',
      name: 'Navbar',
      component: Navbar
    },
    {
      path: '/highlights',
      name: 'Highlights',
      component: Highlights
    },
    {
      path: '/axios/:dept',
      name: 'Axios',
      component: Axios
    },
    {
      path: '/workshops',
      name: 'Workshops',
      component: Workshops

    },
    {
      path: '/generals',
      name: 'Generals',
      component: Generals

    },
    {
      path: '/events',
      name: 'Events',
      component: Events

    },
    {
      path: '/events/:dept',
      name: 'Departments',
      component: Departments

    },
    {
      path: '/events/details/:dept/:ecode',
      name: 'DetailsEvents',
      component: DetailsEvents

    },
    {
      path: '/generals/details/:ecode',
      name: 'DetailsGenerals',
      component: DetailsGenerals

    },
    {
      path: '/workshops/details/:ecode',
      name: 'DetailsWorkshops',
      component: DetailsWorkshops

    },
    {
      path: '/team',
      name: 'Team',
      component: Team

    },
    {
      path: '/contact',
      name: 'Contact',
      component: Contact

    }
  ]
})



