<template>
  <div class="Axios">
    <div v-bind:key="info.code" v-for="info in infos.data.event">
      {{ info.code }}
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Vue',
  data () {
    return {
      infos: null
    }
  },
  mounted () {
    /* eslint-disable */
    console.log('https://dyuksha.org/api/events/' + this.$route.params.dept)

    if (this.$route.params.dept !== '') {
      axios
        .get('https://dyuksha.org/api/events/' + this.$route.params.dept,)
        .then(response => (this.infos = response))
    }
  }
}
</script>

<style scoped>

</style>



