<template>
  <div id="Generals">
    <Navbar/>
    <Rain/>
    <div class="container pt-5" >
        <div class="col-md-12">
            <div class="row" style="padding: 0!important;">
              <div class="col-12">
                <h1>Workshops</h1>
              </div>
              <!-- <div class="col-lg-4">
                <img src="@/assets/images/e.png" alt="casette" class="mcasette" />
              </div> -->
              <div class="col-lg-12 content col-centered">
                <div class="row crow" >
                  <div v-bind:key="info.code" v-for="info in infos.data.workshop" class="card col-md-2 col-centered">
                    <center>
                    <img :src="getImgUrl(info.code)" v-bind:alt="info.code"  class="scasette">
                    </center>
                    <h6><a v-bind:href="'/workshops/details/'+info.code" >View details</a></h6>
                  </div>
                </div>
              </div>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
import Navbar from '@/components/Navbar'
import Rain from '@/components/Rain'
import axios from 'axios'

export default {
  Name: 'Generals',
  data () {
    return {
      infos: null
    }
  },
  components: {
    Navbar,
    Rain
  },
  mounted () {
    /* eslint-disable */
    console.log('https://dyuksha.org/workshops')

    if (this.$route.params.dept !== '') {
      axios
        .get('https://dyuksha.org/api/workshops')
        .then(response => (this.infos = response))
    }
  },
  methods: {
    getImgUrl(card) {
    var images = require.context('@/assets/images/cards', false, /\.png$/)
    return images('./' + card + ".png")
  }
  }
}
</script>
<style>
@import url('https://fonts.googleapis.com/css?family=Blinker&display=swap');
@import url("https://fonts.googleapis.com/css?family=Press+Start+2P");
body{
  width: 100vw;
  background: linear-gradient(to bottom, #202020, #111119);
  /* overflow: hidden; */
}
#app{
  max-width: 100vw;
}
</style>
<style scoped>
  @import '../assets/styles/listing.css';
</style>



