<template>
  <div id="Highlights">
    <Navbar/>
    <Rain/>
    <div class="special pt-5">
      <h1 class="title pt-5">Special Performances</h1>
      <div class="special-performance">
        <h3>DAY 1</h3>
        <h1>Sidharth Menon</h1>
        <img src="@/assets/images/highlights/guest1.png" alt="Day1" srcset="">
      </div>
      <div class="special-performance">
        <h3>DAY 2</h3>
        <h1>Gowry Lekshmi</h1>
        <img src="@/assets/images/highlights/guest2.png" alt="Day1" srcset="">
      </div>
      <div class="special-performance">
        <h3>DAY 3</h3>
        <h1>Pinapple Express</h1>
        <img src="@/assets/images/highlights/guest3.png" alt="Day1" srcset="">
      </div>
    </div>
    <!-- <div class="informals">
      <h1 class="title">Informals</h1>
    </div> -->
  </div>
</template>

<script>
import Navbar from '@/components/Navbar'
import Rain from '@/components/Rain'

export default {
  name: 'Highlights',
  components: {
    Navbar,
    Rain
  }
}
</script>
<style>
body{
  background: linear-gradient(to bottom, #202020, #333333, #111119);
}
</style>
<style scoped>
@import url("https://fonts.googleapis.com/css?family=Blinker&display=swap");

#Highlights{
  margin: 0;
  padding: 0;
  padding-bottom: 50px;
  overflow: auto;
  font-family: blinker;
}
.title{
  color: #E4D8B4;
}
.special, .informals{
  text-align: center;
  margin:0;
  padding: 0;
}
.special-performance{
  position: relative;
  overflow: hidden;
  padding: 0;
  background: #8FB0A9;
}
.special-performance img{
  width: 50%;
  position: relative;
  bottom: 0;
  left: 100%;
  margin-top: 20px;
  margin-bottom: -10px;
  transform: translateX(-150%);
}
.special-performance h1{
  font-size: 30px;
  max-width: 50%;
  padding: 5px;
  position: absolute;
  top: 50%;
  left: 25%;
  transform: translate(-50%,-50%);
}
.special-performance h3{
  position: absolute;
  margin: 0;
  top: 20px;
  left: 20px;
}
@media (orientation: landscape){
  .special-performance{
    float: none;
    margin: 0;
    padding: 0;
    display: inline-block;
    width: 32%;
  }
}
@media (orientation: portrait ){
  .special-performance{
    float: none;
    margin: 0;
    padding: 0;
    display: inline-block;
    width: 100%;
  }
}
</style>



