      <div class="piece output">
        <h1 class="abth1">About D'20</h1>
        <p class="abtp">
          Palakkad has fantastic things tucked away among its folds and among these things is the NSS College of Engineering. It is best described as a place which is bordered by verdurous mountains, rather than walls, where youngsters from all over the state strive together as a vibrant community. Dyuksha'20 is a remarkable vision propelled by an incredible reason and brought about by a strong spark which makes one's contemplations break bonds to sustain a place where nothing and literally nothing is unimaginable. The national level techno-cultural fest is innovation and technology acting together. The intention of Dyuksha 20 is to compose a definitive outlay of innovation, business, culture and flair.
